form {
  display: block;
  text-align: center;
}

.container input {
  border: 1px solid rgb(243, 243, 243);
  /* border-bottom: 1px solid #1e5799; */
  /* box-shadow: 6px 6px 10px rgba(0, 0, 0, 0.03); */
  /* color: rgb(102, 102, 102); */
  color: black;
  display: block;
  margin: 0 auto 15px;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  width: 250px;
  padding: 10px 15px;
  border-radius: 30px;
  border: 2px solid rgb(43, 158, 187);
  /* border: 2px solid rgb(19, 121, 146); */
}

.container input:hover {
  /* border: 2px solid #3b7cc5; */

  border: 2px solid rgb(19, 121, 146);
}

.container input:focus {
  outline: 0;
}

.container .container-forms .container-info {
  font-size: 0;
  text-align: left;
}

.container .container-forms .container-info .info-item {
  color: rgb(255, 255, 255);
  display: inline-block;
  font-size: 16px;
  height: 320px;
  opacity: 1;
  text-align: center;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  vertical-align: top;
  width: 300px;
}

.container .container-forms .container-info .info-item p {
  font-size: 20px;
  margin: 20px;
}

.container .container-forms .container-info .info-item .btn {
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgb(255, 255, 255);
}

