  color: white;
  text-align: left;
  display: flex;
 flex-direction: column;
 margin-top: 56px;
}

/* img {
  height: 200px;
  
} */

.hero::after {
  content: "";
  display: block;
  opacity: .4;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: black;
}

.hero * {
  position: relative;
  z-index: 2;
}

.hero h1, .hero h2 {
  color: white;
}

.jumbotron h1 {
  font-size: 55px;
  padding-top: 60px;
  /* margin-top: 0; */
}

